import { motion } from 'framer-motion';
import Locaux from '../assets/locaux.jpg';
import SolarPanel from '../assets/solaire.jpg';
import Valorisation from '../assets/valorisation.png';
import './engagements.css';

const imageAnimateFromLeft = {
    offscreen: { x: -100, opacity: 0 },
    onscreen: {
        x: 0,
        opacity: 1,
        transition: { duration: 2 },
    },
};

const imageAnimateFromRight = {
    offscreen: { x: 100, opacity: 0 },
    onscreen: {
        x: 0,
        opacity: 1,
        transition: { duration: 2 },
    },
};

const textAnimate = {
    offscreen: { opacity: 0 },
    onscreen: { opacity: 1, transition: { duration: 1 } },
};

function Engagements() {
    return (
        <div className="EngagementsGlobal">
            <motion.div
                className="EngagementsContainer"
                initial={'offscreen'}
                whileInView={'onscreen'}
                viewport={{ once: true, amount: 0.6 }}
                transition={{ staggerChildren: 0.6 }}
            >
                <motion.div
                    className="EngagementsImage"
                    variants={imageAnimateFromLeft}
                >
                    <img src={Locaux} alt="locaux"></img>
                </motion.div>
                <motion.div className="EngagementsText" variants={textAnimate}>
                    <h2>Des produits frais et locaux</h2>
                    <h3>
                        L'intgralit de nos produits sont naturels et issus de
                        l'agriculture biologique, prpars par nos soins, afin
                        de vous faire dcouvrir une grande slection de plats
                        d'ailleurs, tout en restant en Normandie !
                    </h3>
                    <ul>
                        <li>
                            Nos fruits, lgumes et plantes aromatiques sont
                            issus de notre jardin potager.
                        </li>
                        <li>
                            Nos ufs, poulets et bovins sont levs en plein air
                            dans notre ferme.
                        </li>
                        <li>
                            Nos moules sont issues de diffrents levages
                            (corde, fond et bouchot).
                        </li>
                        <li>
                            Notre crme frache nous est fournie par notre
                            partenaire Isigny-ste-Mre, situe dans le Calvados.
                        </li>
                    </ul>
                </motion.div>
            </motion.div>
            <motion.div
                className="EngagementsContainer"
                initial={'offscreen'}
                whileInView={'onscreen'}
                viewport={{ once: true, amount: 0.5 }}
                transition={{ staggerChildren: 0.5 }}
            >
                <motion.div className="EngagementsText" variants={textAnimate}>
                    <h2>Modernisation de notre restaurant</h2>
                    <h3>
                        L'infrastructure de notre restaurant a t tudie afin
                        de respecter au mieux lenvironnement, en tant moins
                        nergivore (surfaces optimises, volution des
                        matriaux), et moins polluante.
                    </h3>
                    <ul>
                        <li>
                            Mise en place de 20 panneaux solaires, fournissant
                            une puissance nominale de 6 kWc.
                        </li>
                        <li>
                            Automatisation de notre compteurs deau permettant
                            d'analyser notre consommation deau.
                        </li>
                        <li>
                            Rcupration de l'eau de pluie pour lalimentation
                            des sanitaires et le nettoyage du sol.
                        </li>
                    </ul>
                </motion.div>
                <motion.div
                    className="EngagementsImage"
                    variants={imageAnimateFromRight}
                >
                    <img src={SolarPanel} alt="Panneaux solaires"></img>
                </motion.div>
            </motion.div>
            <motion.div
                className="EngagementsContainer"
                initial={'offscreen'}
                whileInView={'onscreen'}
                viewport={{ once: true, amount: 0.5 }}
                transition={{ staggerChildren: 0.5 }}
            >
                <motion.div
                    className="EngagementsImage"
                    variants={imageAnimateFromLeft}
                >
                    <img src={Valorisation} alt="Compost"></img>
                </motion.div>
                <motion.div className="EngagementsText" variants={textAnimate}>
                    <h2>Valorisation de nos dchets</h2>
                    <h3>
                        Nous avons la volont de lutter contre la consommation
                        excessive et le gaspillage alimentaire. Pour cela, nous
                        avons mis en place un systme de valorisation de
                        l'ensemble de nos dchets.
                    </h3>
                    <ul>
                        <li>
                            Des Doggy bag sont disponibles dans notre restaurant
                             la demande du client.
                        </li>
                        <li>
                            Les compteurs deau nous alerte en cas de
                            consommations excessives.
                        </li>
                        <li>
                            100% de nos huiles alimentaires usages sont
                            collectes et transformes en biocarburant.
                        </li>
                        <li>
                            Nous sommes en lien troit avec l'agence de la
                            transition cologique : https://www.ademe.fr/.
                        </li>
                    </ul>
                </motion.div>
            </motion.div>
        </div>
    );
}

export default Engagements;
