import React from 'react';
import { useNavigate } from 'react-router-dom';
import { signOut } from 'firebase/auth';
import { auth } from '../../firebase-config';

function PrivateHome() {
    const navigate = useNavigate();

    const logOut = async () => {
        console.log('cliqu');
        try {
            await signOut(auth);
            navigate('/');
        } catch {
            alert(
                'Un problme est survenu, veuillez ressay de vous dconnecter.'
            );
        }
    };

    return (
        <div>
            <br />
            <h4>Vous tes dsormais connect !</h4>
            <br />
            <button onClick={logOut}>Se dconnecter</button>
        </div>
    );
}

export default PrivateHome;
